import { CommonModule} from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { RouterModule } from '@angular/router';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { environment } from '../../../../../environments/environment';

@Component({
  selector: 'app-news-blogs',
  standalone: true,
  imports: [CommonModule, RouterModule, HttpClientModule],
  templateUrl: './news-blogs.component.html',
  styleUrl: './news-blogs.component.scss'
})
export class NewsBlogsComponent implements OnInit {

  selectedSection: string = 'news'; // default
  newsList: any[] = [];
  blogList: any[] = [];
  loading: boolean = true;

  constructor(private http: HttpClient) {}

  ngOnInit(): void {
    this.fetchNews();
    this.fetchBlogs();
  }

  fetchNews() {
    if (this.newsList.length) return;
    this.http.get<any>(`${environment.apiUrl}/news`).subscribe({
      next: (res) => {
        if (res?.success && res.data?.data) {
          this.newsList = res.data.data;
        }
        this.loading = false;
      },
      error: (err) => {
        console.error('Error fetching news:', err);
        this.loading = false;
      }
    });
  }
  fetchBlogs() {
    this.http.get<any>(`${environment.apiUrl}/blogs`).subscribe({
      next: (res) => {
        if (res?.success && res.data?.data) {
          this.blogList = res.data.data;
        }
        this.loading = false;
      },
      error: (err) => {
        console.error('Error fetching news:', err);
        this.loading = false;
      }
    });
  }

  showSection(type: string) {
    this.selectedSection = type;
  }
}
